
#portfolio {

	.container {

		width: calc(100% + 30px);
		height: calc(100% + 30px);

		position: relative;
		left: -15px;

		overflow: auto;

		.item {

			display: block;
			float: left;

			&.one {

				width: 25%; }

			&.two {

				width: 50%; }

			&.three {

				width: 75%; }

			.element {

				width: calc(100% - 30px);
				height: calc(100% - 30px);

				margin: 15px;

				background: black; } } } }
