
#brands {

	height: 100%;
	overflow: auto;

	position: relative;

	text-align: center;

	.container {

		display: inline-block;
		position: relative;

		.item {

			display: inline-block;

			width: 100%;
			height: 120px;
			line-height: 120px;

			margin: 0;

			@include sm {

				width: 130px;
				height: 130px;
				line-height: 130px; }

			@include lg {

				width: 160px;
				height: 160px;
				line-height: 160px; }

			@include ul {

				width: 280px;
				height: 280px;
				line-height: 280px; }

			img {

				display: block;

				margin: 0 auto;

				width: auto;
				height: 100%; } } }

	.note {

		font-size: 14px;
		line-height: 1.4em;

		@include sm {

			position: absolute;
			bottom: 0;
			left: 0;

			text-align: left; } } }
